.Card {
  margin: 100px auto 50px;
  padding: 20px;
  width: 500px;
  height: 312px;
  border-radius: 20px;
  background-image: url("http://www.brandgradients.com/img/backgrounds/mastercard-hex-colors-gradient-background.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
img {
  width: 120px;
  padding-left: 15px;
}
h1 {
  text-align: right;
  padding-right: 25px;
  margin-bottom: 0px;
  color: white;
}
.data {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.MONYEAR {
  font-size: 20px;
  margin: 0px;
  display: block;
}
.MONTHYEAR {
  font-size: 8px;
  margin: 0px;
}
.num {
  font-size: 25px;
  margin: 0px;
}
.number {
  font-size: 30px;
  margin: 0px;
  letter-spacing: 1px;
  word-spacing: 1px;
}
.master_logo {
  height: 90px;
  width: 70px;
}
.cardholder {
  font-size: 22px;
  margin: 0px;
  padding-top: 10px;
}
.info {
  width: 350px;
  padding-left: 25px;
}
.inner_info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.validation {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.triangle {
  align-self: center;
  padding-bottom: 5px;
}
.validthru {
  font-size: 10px;
  margin: 0px 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.thru {
  display: block;
}
